/**
 * Base
 */
// Canvas
const canvas = document.querySelector('canvas.webgl')

// Scene
const scene = new THREE.Scene()

const ambLight= new THREE.AmbientLight( 0xffffff, 1.1 ); // soft white light
scene.add(ambLight);

const lightMaterial = new THREE.MeshBasicMaterial({color: 0xffbb00})



/**
 * Sizes
 */
// const sizes = {
//     width: window.innerWidth,
//     height: window.innerHeight
// }

window.addEventListener('resize', () =>
{
  const width = canvas.clientWidth;
  const height = canvas.clientHeight;
  if (canvas.width !== width ||canvas.height !== height) {
    if (width>=height){
      renderer.setPixelRatio((window.devicePixelRatio*canvas.clientWidth*0.001))
    } else {
      renderer.setPixelRatio((window.devicePixelRatio*canvas.clientHeight*0.001))
    }
    // you must pass false here or three.js sadly fights the browser
    renderer.setSize(width, height, false);
    camera.aspect = width / height;
    camera.updateProjectionMatrix();
    // set render target sizes here
  }
})

function resizeCanvasToDisplaySize() {
    // const canvas1 = renderer.domElement;
    const width = canvas.clientWidth;
    const height = canvas.clientHeight;
    if (canvas.width !== width ||canvas.height !== height) {
      // you must pass false here or three.js sadly fights the browser
      renderer.setSize(width, height, false);
      camera.aspect = width / height;
      camera.updateProjectionMatrix();
      if (width>=height){
        renderer.setPixelRatio((window.devicePixelRatio*canvas.clientWidth*0.001))
      } else {
        renderer.setPixelRatio((window.devicePixelRatio*canvas.clientHeight*0.001))
      }
      
      // set render target sizes here
    }
}

const renderer = new THREE.WebGLRenderer({
    canvas: canvas,
    alpha: true,
    antialias: true
})

renderer.setClearColor(0x000000, 0);
renderer.setPixelRatio((window.devicePixelRatio*canvas.clientHeight*0.005))

/**
 * Camera
 */
// Base camera
const camera = new THREE.PerspectiveCamera(50, 2, 1, 30000)
camera.position.set(60,30,70)
camera.lookAt(-1,20,0)
scene.add(camera)

/**
 * Renderer
 */


let mixer = null

// gltfLoader.load('toyota_ae86_scene - Copy/ae86.gltf', function (gltf){
