//COMPOSER
// var parameters = { minFilter: THREE.LinearFilter, magFilter: THREE.LinearFilter, format: THREE.RGBAFormat, stencilBuffer: true };

// var renderTarget = new THREE.WebGLRenderTarget( sizes.width, sizes.height, parameters );
var composer = new EffectComposer(renderer);
var renderPass = new RenderPass(scene,camera);
// renderPass.clearColor = new THREE.Color(0, 0, 0);
// renderPass.clearAlpha = 0;
composer.addPass(renderPass);


// var ssrPass = new SSRPass();
// composer.addPass(ssrPass);

//custom shader pass
var vertShader = document.getElementById('vertexShader').textContent;
var fragShader = document.getElementById('fragmentShader').textContent;
var counter = 0.0;
var myEffect = {
  uniforms: {
    "tDiffuse": { value: null },
    "amount": { value: counter }
  },
  vertexShader: vertShader,
  fragmentShader: fragShader
}

var customPass = new ShaderPass(myEffect);
customPass.renderToScreen = true;
composer.addPass(customPass);

const rgbShiftPass = new ShaderPass(RGBShiftShader);
rgbShiftPass.uniforms['amount'].value = 1.2/canvas.clientWidth;
// rgbShiftPass.uniforms['resolution'].value(1/window.innerWidth,1/window.innerHeight)

var cPass = new ShaderPass(ACESFilmicToneMappingShader);
cPass.renderToScreen = true
cPass.material.transparent = true;
composer.addPass(cPass);



var fxaaPass = new ShaderPass(FXAAShader);
fxaaPass.uniforms.resolution.value.set(1/canvas.clientWidth,1/canvas.clientHeight);
// fxaaPass.renderToScreen = true;
// fxaaPass.material.transparent = true; // FIX
composer.addPass(fxaaPass);


composer.addPass(rgbShiftPass);
// composer.addPass(gammaCorrectionPass)


function unPauseAllActions() {

    // actions.forEach( function ( action ) {

    //     action.paused = true;

    // } );

}

let singleStepMode = false;
let sizeOfNextStep = 0;

function toSingleStepMode() {

    unPauseAllActions();
    console.log("hi")
    singleStepMode = true;
    sizeOfNextStep = 1;

}
/**
 * Animate
 */
const clock = new THREE.Clock()
let previousTime = 0;
const tick = () =>
{
    const elapsedTime = clock.getElapsedTime()
    const deltaTime = elapsedTime - previousTime
    previousTime = elapsedTime

    if( mixer !== null){
        mixer.update(deltaTime)
    }

    // Render
    // renderer.render(scene, camera)

    counter += 0.0000005;
    customPass.uniforms["amount"].value = counter;

    resizeCanvasToDisplaySize();
    
    // Call tick again on the next frame
    window.requestAnimationFrame(tick)
    composer.render();
}
